    // return value.replace(/[a-zA-Z0-9\u0080-\u00FF\u222E\u03BC\u03A6\u2109\u03A9\u33A1\u2265\u2264\u2103\u338E\u338F\u339C\u339d\u33c4\u33ce\u2022\u2013\u2032\u03C6\u2606\u2764\u27B7\u2B50\u2011\u3000\u2026\u03A9\u03C9\u2460-\u2469\u2122\u2266\u0424\u203B\u2080-\u2089\u25CF!@%&()/+"#;:?|*'`^(),.~_ <>=\n\r\t-[\]\\]+/ig, '')
    return (value && value.search(/[\u4e00-\u9fa5]/) >= 0) ? 'Chinese Characters' : ''
