  getProductEditUrl (component, productId, source, autoSave, viewOnly) {
    let routeData = component.$router.resolve({path: '/products/products/productedit',
      query: {product_id: productId, source: source, auto_save: autoSave ? 1 : 0, view_only: viewOnly ? 1 : 0}})
    return routeData
  },

  openProductEditPageEx (component, productId, source, autoSave) {
    let routeData = this.getProductEditUrl(component, productId, source, autoSave)
    window.open(routeData.href, '_blank')
  },

  openProductEditPage (component, productId, source) {
    this.openProductEditPageEx(component, productId, source, 0)
  },

  getDateFromDateTimeString (tzStr) {
    // Get data part from time string like this: 2020-03-08T08:00:00Z
    return tzStr.split('T')[0]
  },

  getImageThumbById (vue, imageId, size) {
    let link = `${vue.img_root}thumb/?id=${imageId}`
    if (size) {
      link = `${link}&thumb_size=${size}`
    }
    return link
  },

  getImageThumbByName (vue, imageName, size) {
    let link = `${vue.img_root}thumb/${imageName}`
    if (size) {
      link = `${link}?thumb_size=${size}`
    }
    return link
  },

  getImageLinkByName (vue, imageName) {
    return `${vue.img_root}${imageName}`
  },

  getLabelFromOptionMap (optionMap, valueKey, labelKey, value) {
    let option = optionMap.find((v) => (v[valueKey] === value))
    return option[labelKey]
  },

  getDayDiff (endTime, startTime) {
    return (new Date(endTime).getTime() - new Date(startTime)) / 86400000
  },

