<template>
  <ModalDlgFrame :modalId="modalId" :dialogWidth=300 :titleBg="titleBg" :showClose="false">
    <template v-slot:title>
    <div>
      {{ title }}
    </div>
    </template>
    <template v-slot:body>
    <div >
      <div class="spinner-border text-primary" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
    </template>
  </ModalDlgFrame>
</template>

<script>
// axios
export default {
  name: 'SimpleModalDlg',
  data () {
    return {
    }
  },
  props: {
    modalId: {
      type: String,
      required: false,
      default: 'waitingModal'
    },
    titleBg: {
      type: String,
      required: false,
      default: 'bg-warning'
    },
    title: {
      type: String,
      required: false,
      default: "I'm working hard..."
    }
  },
  mounted () {
  }
}
</script>
