<template>
  <div class="container-fluid">
    <div class="row w-100 ml-1 my-2">
      <button class="btn btn-primary" data-toggle="modal" :data-target="'#' + channelEditDlgId"></button>
      <button class="btn btn-light ml-4" :disabled="true"> {{usedNos.length}}/{{availNos.length}}</button>
    </div>
    <div class="row w-100">
      <ApiDataTable
        :handlers="tableHandlers"
        tableId="channelListTableId"
        :dataHeight=400
        :showWaitingDlg='false'
        :showSearch="false"
        :detailView="false"
      ></ApiDataTable>
    </div>
    <ChannelEditDlg
      :modalId="channelEditDlgId"
      :channelId="currRowChannelId"
      :availNos="availNos"
      @channel-saved="onChannelSaved"
    ></ChannelEditDlg>
    <ChannelDeleteDlg
      :modalId="channelDeleteDlgId"
      :channelId="currRowChannelId"
      :cno="currRowChannelNo"
      @channel-deleted="onChannelDeleted"
    ></ChannelDeleteDlg>
  </div>
</template>

<script>

import ApiDataTable from '../utils/ApiDataTable'
import utils from '@/utils/utils'
import $ from 'jquery'
import ChannelEditDlg from './ChannelEditDlg'
import ChannelDeleteDlg from './ChannelDeleteDlg'
import apis from '@/apis/apis'

/**
 * 
 * @param {*} row : row data
 */
window.rowSelected = function (channelId, channelNo) {
  window.currRowChannelId = channelId
  window.currRowChannelNo = channelNo
}

export default {
  name: 'ModuleChConfig',
  components: {
    ApiDataTable,
    ChannelEditDlg,
    ChannelDeleteDlg
  },
  data () {
    return {
      channelListTableId: utils.randomId('tableId'),
      channelEditDlgId: utils.randomId('modalId'),
      channelDeleteDlgId: utils.randomId('modalId'),
      tableComp: null,
      // For edit
      currRowChannelId: 0,
      currRowChannelNo: 0,

      freeNos: [1, 2, 3, 4, 5, 6, 7, 8],
      usedNos: [],
    }
  },
  computed: {
    tableHandlers () {
      return {
        setComponent: this.tableSetComponent,
        getEndpoint: this.tableGetEndPoint,
        getParams: this.tableGetParams,
        tableRowFormatter: this.tableRowFormatter,
        getColSpecs: this.tableGetColSpecs,
        rowStyle: this.rowStyle,
        getDataList: this.getDataList
      }
    },
    availNos () {
      let nos = [...this.freeNos, ...this.usedNos]
      return nos.sort()
    }
  },
  methods: {
    onChannelSaved () {
      this.reloadTable()
      this.getUsedFreeChannels()
    },
    onChannelDeleted () {
      this.reloadTable()
      this.getUsedFreeChannels()
    },
    tableSetComponent (tableComp) {
      this.tableComp = tableComp
    },
    tableRowFormatter (r) {
      return {
        cno: r.cno,
        cid: r.cid,
        name: r.name,
        site: r.site,
        url: r.url,
        status: '',
        operation: `
          <a href="javascript:rowSelected(${r.id}, ${r.cno});$('#${this.channelEditDlgId}').modal('show')"></a>
          <a href="javascript:rowSelected(${r.id}, ${r.cno});$('#${this.channelDeleteDlgId}').modal('show')"></a>
        `
      }
    },
    tableGetParams () {
      return {
      }
    },
    tableGetEndPoint () {
      return 'channel/channels/'
    },
    tableGetColSpecs () {
      let tableColumns = [
        {
          field: 'cno',
          title: ''
        },
        {
          field: 'cid',
          title: 'ID'
        },
        {
          field: 'name',
          title: ''
        },
        {
          field: 'site',
          title: ''
        },
        {
          field: 'url',
          title: 'URL'
        },
        {
          field: 'status',
          title: ''
        },
        {
          field: 'operation',
          title: ''
        }
      ]
      return tableColumns
    },
    onTableSelectionChanged () {
      let selectedRows = $('#channelListTableId').bootstrapTable('getSelections')
      this.selectedIds = []
      selectedRows.forEach((item) => {
        this.selectedIds.push(item.id)
      })
      console.log(`onTableSelectionChanged: (${selectedRows.length}): ${this.selectedIds}`)
    },
    reloadTable () {
      if (this.tableComp) {
        this.tableComp.getOnePage()
      }
      this.onTableSelectionChanged()
    },
    rowStyle () {
      let style = {}
      return style
    },
    getDataList (res) {
      return res.data.results
    },
    markSelectedRead () {
      this.markRead(this.selectedIds)
    },
    getUsedFreeChannels () {
      let endpoint = 'channel/channels/get_used_free_channel_nos/'

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            this.freeNos = res.data.free_nos
            this.usedNos = res.data.used_nos
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    beforeModalShow () {
      this.currRowChannelId = window.currRowChannelId
      this.currRowChannelNo = window.currRowChannelNo
    }
  },
  mounted () {
    // If callback is defined as: function(event), then 'this' is the modal, otherwise, 'this' is this component
    $(`#${this.channelEditDlgId}`).on('show.bs.modal', (event) => {
      if ('relatedTarget' in event) {
        // Called by button comes here
        this.currRowChannelId = 0
        this.currRowChannelNo = 0
      } else {
        // console.log('window.currRowChannelId', window.currRowChannelId)
        // this.currRowChannelId = window.currRowChannelId
        this.beforeModalShow()
      }
    })

    $(`#${this.channelDeleteDlgId}`).on('show.bs.modal', () => {
      this.beforeModalShow()
    })

    this.getUsedFreeChannels()
  }
}

</script>