<template>
  <SimpleModalDlg :modalId="modalId" :title="channelId?'':''" content="" defaultButtonText="" button2Text=""
    :button2Action="button2Action" :dialogWidth="600">
    <template v-slot:bodyPost>
      <div class="container-fluid">
        <!-- <div class="row"> -->
        <div class="row">
          <div class="col-3">
            <span class="field-label field-required"></span>
          </div>
          <div class="col mb-1">
            <select name="selectList" v-model="cno">
              <option v-for="no in availNos" :key="modalId+no">{{no}}</option>
            </select>
            <!-- <input class="mr-1" style="width:20em;" v-model="cno" /> -->
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="cnoValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
        <div class="row">
          <div class="col-3">
            <span class="field-label field-required">ID</span>
          </div>
          <div class="col mb-1">
            <input class="mr-1" style="width:20em;" v-model="cid" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="cidValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
        <div class="row">
        <div class="col-3">
          <span class="field-label field-required"></span>
          </div>
          <div class="col mb-1">
            <input class="mr-1" style="width:20em;" v-model="name" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="nameValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
        <div class="row">
          <div class="col-3">
            <span class="field-label field-required"></span>
          </div>
          <div class="col mb-1">
            <input class="mr-1" style="width:20em;" v-model="site" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="siteValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
        <div class="row">
          <div class="col-3">
            <span class="field-label field-required">URL</span>
          </div>
          <div class="col mb-1">
            <input class="mr-1" style="width:20em;" v-model="url" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="urlValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
        <!-- </div> -->
      </div>
    </template>
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'

export default {
  name: 'ChannelEditDlg',
  components: {
    SimpleModalDlg,
  },
  data () {
    return {
      // Below is the data fields
      cno: '',
      cid: '',
      name: '',
      site: '',
      url: '',
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    channelId: {
      type: Number,
      required: true
    },
    availNos: {
      type: Array,
      required: true
    }
  },
  computed: {
    cnoValid () {
      return !!this.cno
    },
    cidValid () {
      return this.cid.trim().length == 32
    },
    nameValid () {
      return this.name.trim().length > 0
    },
    siteValid () {
      return this.site.length > 0
    },
    urlValid () {
      return this.url.length >= 10
    }
  },
  methods: {
    button2Action () {
      let endpoint = 'channel/channels/'

      let data = {}

      // 
      if (!this.cnoValid) {
        alert('')
        return
      }
      data['cno'] = this.cno

      if (!this.cidValid) {
        alert('ID3216')
        return
      }
      data['cid'] = this.cid.trim()
    
      if (!this.nameValid) {
        alert('')
        return
      }
      data['name'] = this.name.trim()

      if (!this.siteValid) {
        alert('')
        return
      }
      data['site'] = this.site.trim()

      if (!this.urlValid) {
        alert('URL')
        return
      }
      data['url'] = this.url.trim()

      let request = apis.apiAjaxPost
      if (this.channelId) {
        // channelId
        endpoint = `${endpoint}/${this.channelId}/`
        request = apis.apiAjaxPut
      }

      request(endpoint, data,
        (res) => {
          if (res.code === 0) {
            alert('')
            this.$emit('channel-saved')
          } else {
            alert(`(${res.code}): ${res.message}`)
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    clearFields () {
      this.cno = ''
      this.cid = ''
      this.name = ''
      this.site = ''
      this.url = ''
    },
    loadChannel () {
      let endpoint = `channel/channels/${this.channelId}/`
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            this.cno = res.data.cno
            this.cid = res.data.cid
            this.name = res.data.name
            this.site = res.data.site
            this.url = res.data.url
          } else {
            alert(`(${res.code}): ${res.message}`)
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  watch: {
    channelId (value) {
      if (!value) {
        this.clearFields()
      } else {
        this.loadChannel()
      }
    }
  },
  mounted () {
  }
}
</script>
