<template>
  <div class='container-fluid'>
    <table
      class="text-nowrap"
      :id="tableId"
      :data-height="dataHeight"
      data-classes="table table-striped table-sm"
      data-sortable="true"
      data-click-to-select="true"
      :data-pagination="pagination"
      data-side-pagination="server"
      :data-detail-view="detailView"
      data-detail-view-icon="true"
      data-detail-view-by-click="true"
      :data-show-footer="showFooter"
    ></table>
    <WaitingDlg v-if="!this.inModal && this.showWaitingDlg"></WaitingDlg>
  </div>
</template>

<script>
import eventtrans from '@/utils/eventtrans'
import $ from 'jquery'
import WaitingDlg from '@/components/utils/WaitingDlg'
import apis from '@/apis/apis'

export default {
  name: 'ApiDataTable',
  components: {
    WaitingDlg
  },
  data () {
    return {
      table: null,

      listSize: 10,
      listOffset: 0,
      searchText: '',
      sortOrder: '',
      sortField: '',

      timeoutIdReload: 0,

      selectedIds: [],
      selectedRows: []
    }
  },
  props: {
    tableId: {
      type: String,
      required: false,
      default: 'api_data_table'
    },
    handlers: {
      type: Object,
      required: true
    },
    dataHeight: {
      type: Number,
      required: false,
      default: 500
    },
    loadData: {
      type: Boolean,
      required: false,
      default: true
    },
    inModal: {
      type: Boolean,
      required: false,
      default: false
    },
    showWaitingDlg: {
      type: Boolean,
      required: false,
      default: false
    },
    showSearch: {
      type: Boolean,
      required: false,
      default: true
    },
    showFooter: {
      type: Boolean,
      required: false,
      default: false
    },
    detailView: {
      type: Boolean,
      required: false,
      default: true
    },
    pagination: {
      type: Boolean,
      required: false,
      default: true
    },
    checkbox: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  methods: {
    detailFormatter (index, row) {
      if (typeof this.handlers.detailFormatter === 'undefined') {
        return this.defaultDetailFormatter(index, row)
      } else {
        return this.handlers.detailFormatter(index, row)
      }
    },

    rowStyle (row, index) {
      if (typeof this.handlers.rowStyle === 'undefined') {
        return {}
      } else {
        return this.handlers.rowStyle(row, index)
      }
    },

    defaultDetailFormatter (index, row) {
      var html = []
      html.push('<div class="text-left"><pre>')
      let dstr = JSON.stringify(row, null, 4)
      html.push(dstr)
      html.push('</pre></div>')
      return html.join('')
    },
    getOnePage () {
      console.log('getOnePage')
      let endpoint = this.handlers.getEndpoint()
      if (!endpoint) {
        return
      }
      let params = {limit: this.listSize, offset: this.listOffset, search: this.searchText, order: this.sortOrder, order_field: this.sortField}
      Object.assign(params, this.getSortParam())
      if ('getParams' in this.handlers && this.handlers.getParams) {
        let params2 = this.handlers.getParams()
        if (params2) {
          params = {...params, ...params2}
        }
      }
      if (!this.inModal && this.showWaitingDlg && this.listSize > 20) {
        $('#waitingModal').modal('show')
      }
      apis.apiAjaxGet(endpoint,
        params,
        (res) => {
          if (!this.inModal && this.showWaitingDlg && this.listSize > 20) {
            $('#waitingModal').modal('hide')
          }

          if (res.code === 0) {
            let data = res.data
            // console.log('Got One Page', data)

            let dataList = this.handlers.getDataList ? this.handlers.getDataList(res) : data.rows

            let rows = []
            for (let i in dataList) {
              let row = this.handlers.tableRowFormatter(dataList[i], i, this)
              rows.push(row)
            }
            // console.log(rows)
            this.table.bootstrapTable('load', rows)
            this.table.bootstrapTable('refreshOptions', {totalRows: data.count})
          } else {
            console.log('Got getOnePage error: ')
            console.log(res.data)
          }
        },
        (err) => {
          if (!this.inModal && this.showWaitingDlg) {
            $('#waitingModal').modal('hide')
          }

          console.log(err)
        },
        () => {
          // Finally here.
          // Anyway, we refresh the selection
          this.onTableSelectionChanged()
          this.onTableDblClickCell(null, null, null, null)
        }
      )
    },
    onSearch (e, text) {
      // This will cause onPage change, so do not call getOnePage!!!
      this.searchText = text
      console.log('onSearch', text)
    },
    onPageChange (e, number, size) {
      console.log('onPageChange: ', number, size)
      this.listSize = size
      this.listOffset = (number - 1) * this.listSize
      // As 'search.bs.table' event always comes after page change, we do noting in on search, but get search text here
      this.searchText = this.table.bootstrapTable('getOptions').searchText
      // this.sortOrder = this.table.bootstrapTable('getOptions').sortOrder
      this.getOnePage()
    },
    getSortParam () {
      let options = this.table.bootstrapTable('getOptions')

      let field = options.sortName
      let order = options.sortOrder

      if (options.sortName) {
        field = options.sortName
        order = options.sortOrder
      } else {
        // If there is no user specified ordering, use default
        let defaultSortColumn = options.columns[0].find(item => 'default-sort-order' in item)
        if (defaultSortColumn) {
          field = defaultSortColumn.field
          order = defaultSortColumn['default-sort-order']
        } else {
          return {}
        }
      }

      let sortField = field
      let sortColumn = options.columns[0].find(item => item.field === field)
      if ('sort-name' in sortColumn) {
        sortField = sortColumn['sort-name']
      }
      sortField = {'asc': '', 'desc': '-'}[order] + sortField
      console.log(`onSort: ${field}, ${order}, ${sortField}`, options)
      return {ordering: sortField}
    },
    onTableDblClickCell ($event, field, value, row) {
      console.log('cellSelected', field, value)
      this.$emit('cellSelected', field, value, row)
    },
    getRowIndex (field, value) {
      let index = -1
      let rows = this.table.bootstrapTable('getData')
      rows.forEach((v, i) => {
        if (v[field] === value) {
          index = i
        }
      })
      return index
    },
    refreshSelectedRow (field, newValue, oldValue) {
      // Refresh display of selected row. Often changed from the old value to new value.
      let index = this.getRowIndex(field, newValue)
      if (index >= 0) {
        this.table.bootstrapTable('showRow', {index: index})
      }

      let oldIndex = this.getRowIndex(field, oldValue)
      if (oldIndex >= 0) {
        this.table.bootstrapTable('showRow', {index: oldIndex})
      }
    },
    delayToReleadTable (resetCurrPage) {
      // Delay to minize table reload
      if (this.timeoutIdReload) {
        clearTimeout(this.timeoutIdReload)
        this.timeoutIdReload = 0
      }
      if (resetCurrPage) {
        // Goto first page
        // this.table.bootstrapTable({
        //   'selectPage': 1,
        //   'pageNumber': 1
        // });
        this.listOffset = 0
      }
      this.timeoutIdReload = setTimeout(() => {
        this.timeoutIdReload = 0
        this.getOnePage()
      }, 500)
    },
    onTableSelectionChanged () {
      this.selectedRows = this.table.bootstrapTable('getSelections')
      this.selectedIds = this.selectedRows.map((v) => v.id)
      console.log(`onTableSelectionChanged: (${this.selectedIds.length}): ${this.selectedIds}`)
      this.$emit('selection-changed', this.selectedIds, this.selectedRows)
    },
    setEventForCheckbox () {
      if (this.checkbox) {
        this.table.on('check.bs.table', this.onTableSelectionChanged)
        this.table.on('uncheck.bs.table', this.onTableSelectionChanged)
        this.table.on('check-all.bs.table', this.onTableSelectionChanged)
        this.table.on('uncheck-all.bs.table', this.onTableSelectionChanged)
      }
    }
  },
  computed: {
  },
  watch: {
    loadData (val) {
      if (val) {
        this.getOnePage()
      }
    },
    checkbox () {
      this.setEventForCheckbox()
    }
  },
  mounted () {
    this.handlers.setComponent(this)

    let colSpecs = this.handlers.getColSpecs()
    if (this.checkbox) {
      colSpecs = [
        {
          field: 'selected',
          checkbox: true,
          default: false
        },
        ...colSpecs
      ]
    }
    // this.table = $(`#${this.tableId}`)
    let table = $(`#${this.tableId}`)
    this.table = table
    console.log('this.table', this.table, table)
    table.bootstrapTable({
      columns: colSpecs,
      pageNumber: 1,
      pageSize: this.listSize,
      pageList: '[10, 20, 50, 100]',
      paginationHAlign: 'right',
      paginationDetailHAlign: 'left',
      detailFormatter: this.detailFormatter,
      rowStyle: this.rowStyle,
      search: this.showSearch,
      showJumpTo: true
    })
    table.on('search.bs.table', this.onSearch)
    table.on('sort.bs.table', this.getOnePage)
    // this.table.on('sort.bs.table', this.onSort)
    table.on('page-change.bs.table', this.onPageChange)
    table.on('dbl-click-cell.bs.table', this.onTableDblClickCell)
    this.setEventForCheckbox()
    if (this.loadData) {
      this.getOnePage()
    }
  },

  // Below is utilities for other components
  transCellSelected2ItemSelected (comp, field, value, row) {
    eventtrans.transCellSelected2ItemSelected(comp, field, value, row)
  }

}
</script>

<style scoped>
</style>
