<template>
  <div class='container-fluid'>
    <div class="modal fade modal-open" data-backdrop="static" :id="modalId" tabindex="-1" role="dialog">
      <div class="modal-dialog draggable align-self-center" role="document">
        <div class="modal-content"  :style="contentStyle">
          <div :class="'modal-header panel-heading ' + titleBg">
            <h4 class="modal-title">
              <slot name="title"></slot>
            </h4>
            <button v-if="showClose" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true"></span>
            </button>
          </div>
          <div class="modal-body">
            <slot name="body"></slot>
          </div>
          <div class="modal-footer">
            <slot name="footer"></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'ModalDlgFrame',
  props: {
    modalId: {
      type: String,
      required: true
    },
    titleBg: {
      type: String,
      required: false,
      default: 'bg-primary'
    },
    dialogWidth: {
      type: Number,
      required: false,
      default: 800
    },
    showClose: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  data () {
    return {
    }
  },
  methods: {
    modalCenter () {
      let modalDlg = $(`#${this.modalId} ` + '.modal-dialog')
      // console.log('modal visible', modalDlg.is(':visible'))
      if (!modalDlg.is(':visible')) {
        return
      }

      $(`#${this.modalId}`).css('display', 'block')

      let modalContent = $(`#${this.modalId} ` + '.modal-content')
      let height = modalContent.outerHeight(true)
      let width = modalContent.outerWidth(true)
      // console.log('modal-content: ', width, height)

      let modalTop = $(window).height() / 2 - height / 2
      // If the modalTop is negative, the title is unseen
      if (modalTop < 10) {
        modalTop = 10
      }
      modalDlg.css('margin-top', '' + modalTop + 'px')

      let modalLeft = $(window).width() / 2 - width / 2 // Width always get wrong value!!!
      modalDlg.css('margin-left', '' + modalLeft + 'px')
    }
  },
  computed: {
    contentStyle () {
      return {
        width: this.dialogWidth + 'px'
      }
    }
  },
  created () {
  },
  mounted () {
    console.log('ModalDlgFrame mounted', this.modalId)
    $(`#${this.modalId}`).on('shown.bs.modal', this.modalCenter)
    $(window).on('resize', this.modalCenter)
  }
}
</script>

