// Pending request for cached request
let cachePendings = {}


// Note:
// 'this' is the object itself
export default {
  hello: 'world', // for test only
  getAjaxCacheItem() {
    return false
  },
  saveAjaxCacheItem() {
  },
	apiTest() {
		return httpRequest({
			url: 'ibox/products/?offset=0&limit=10',
			method: 'get',
			data: {}
		})
	},
  apiAjax (endpoint, method, params, dataCb, errCb, finalCb, config) {
    httpRequest({
      method: method,
      url: `${endpoint}`,
      data: params,
      config: config
    }).then(dataCb).catch(errCb).finally(finalCb)
  },
  apiAjaxGet (endpoint, params, dataCb, errCb, finalCb = () => {}, config = {}, wantCache = false, delayed = false) {
    let _dataCb
    let _finalCb

    // Do cache here.
    // If the signature has been wantCache, call dataCb and finalCb, then return
    if (this && wantCache) {
      let signature = endpoint + JSON.stringify(params)

      let res = this.getAjaxCacheItem(signature)

      // console.log(`apiAjaxGet: ${signature}`, this.$store, res)

      if (res) {
        // console.log(`cache hit for: ${signature}`, this.$store, res)

        // Give a copy, otherwise the caller might change it
        let newRes = {...res}
        dataCb(newRes)

        if (finalCb) {
          finalCb()
        }
        return
      } else {
        // If not hit, check if there is some same request pending
        if (signature in cachePendings) {
          if (!delayed) {
            // console.log(`cache not hit, but already some one request, wait: ${signature}`)
            setTimeout(() => {
              this.apiAjaxGet(endpoint, params, dataCb, errCb, finalCb, config, wantCache, true)
            }, 3500)
            return
          } else {
            // If already waited, but not hit cache, then do the actual request
          }
        } else {
          cachePendings[signature] = true
        }
      }

      // Cache the result only if the api return successful
      _dataCb = (res) => {
        // Save only the original res object, dataCb may modify it
        let newRes = {...res}
        this.saveAjaxCacheItem('setAjaxCacheItem', {signature: signature, response: newRes})

        dataCb(res)
      }

      _finalCb = () => {
        // Clear pending if returned from actual request
        delete cachePendings[signature]

        if (finalCb) {
          finalCb()
        }
      }
    } else {
      _dataCb = dataCb
      _finalCb = finalCb
    }

    // this.$apiAjax(endpoint, 'get', params, dataCb, errCb, config)
    httpRequest({
      method: 'GET',
      url: `${endpoint}`,
      params: params,
      config: config
    }).then(_dataCb).catch(errCb).finally(_finalCb)
  },
  apiAjaxPost (endpoint, data, dataCb, errCb, finalCb = () => {}, config = {}, queryParams = {}) {
    // console.log('post', endpoint, params, dataCb, errCb, finalCb, config)
    httpRequest({
      method: 'POST',
      url: `${endpoint}`,
      params: queryParams,
      data: data,
      config: config
    }).then(dataCb).catch(errCb).finally(finalCb)
  },
  apiAjaxPut (endpoint, params, dataCb, errCb, finalCb = () => {}, config = {}) {
    // console.log('post', endpoint, params, dataCb, errCb, finalCb, config)
    httpRequest({
      method: 'PUT',
      url: `${endpoint}`,
      data: params,
      config: config
    }).then(dataCb).catch(errCb).finally(finalCb)
  },
  apiAjaxPostOrPut (isPost, endpoint, params, dataCb, errCb, finalCb = () => {}, config = {}) {
    if (isPost) {
      httpRequest({
        method: 'POST',
        url: `${endpoint}`,
        data: params,
        config: config
      }).then(dataCb).catch(errCb).finally(finalCb)
    } else {
      httpRequest({
        method: 'PUT',
        url: `${endpoint}`,
        data: params,
        config: config
      }).then(dataCb).catch(errCb).finally(finalCb)
    }
  },
  apiAjaxDelete (endpoint, params, dataCb, errCb, finalCb = () => {}, config = {}) {
    // this.$apiAjax(endpoint, 'post', params, dataCb, errCb, config)
    httpRequest({
      method: 'DELETE',
      url: `${endpoint}`,
      data: params,
      config: config
    }).then(dataCb).catch(errCb).finally(finalCb)
  },
  apiAjaxPatch (endpoint, params, dataCb, errCb, finalCb = () => {}, config = {}) {
    // this.$apiAjax(endpoint, 'post', params, dataCb, errCb, config)
    httpRequest({
      method: 'PATCH',
      url: `${endpoint}`,
      data: params,
      config: config
    }).then(dataCb).catch(errCb).finally(finalCb)
  }
}
