export default {
  transCellSelected2ItemSelected (comp, field, value, row) {
    let selectedId = 0
    let selectedRow = null

    if (row) {
      selectedId = row.id
      selectedRow = row
    }

    if ('selectedId' in comp) {
      comp.selectedId = selectedId
    }
    if ('selectedRow' in comp) {
      comp.selectedRow = selectedRow
    }

    comp.$emit('itemSelected', selectedId, selectedRow)
  }
}
