<template>
  <ModalDlgFrame :modalId="modalId" :dialogWidth="dialogWidth" :titleBg="titleBg">
    <template v-slot:title>
      {{ title }}
    </template>
    <template v-slot:body>
        <slot name="bodyPre">
        </slot>
        <pre v-if="content.length > 0" class="text-left">{{ content }}</pre>
        <slot name="bodyPost">
        </slot>
    </template>
    <template v-slot:footer>
      <slot name="footerPre">
      </slot>
      <button type="button" class="btn btn-default btn-primary" data-dismiss="modal" @click="defaultButtonAction">
        {{ defaultButtonText }}
      </button>
      <button v-if="button2Text" type="button" class="btn btn-outline-primary" data-dismiss="modal" @click="button2Action">
        {{ button2Text }}
      </button>
      <button v-if="button3Text" type="button" class="btn btn-outline-primary" data-dismiss="modal" @click="button3Action">
        {{ button3Text }}
      </button>
      <slot name="footerPost">
      </slot>
    </template>
  </ModalDlgFrame>
</template>

<script>
import ModalDlgFrame from './ModalDlgFrame.vue'

// axios
export default {
  name: 'SimpleModalDlg',
  components: {
    ModalDlgFrame
  },
  data () {
    return {
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    titleBg: {
      type: String,
      required: false,
      default: 'bg-warning'
    },
    defaultButtonText: {
      type: String,
      required: false,
      default: 'Close'
    },
    defaultButtonAction: {
      type: Function,
      required: false,
      default: () => {}
    },
    button2Text: {
      type: String,
      required: false,
      default: ''
    },
    button2Action: {
      type: Function,
      required: false,
      default: () => {}
    },
    button3Text: {
      type: String,
      required: false,
      default: ''
    },
    button3Action: {
      type: Function,
      required: false,
      default: () => {}
    },
    title: {
      type: String,
      required: false,
      default: 'Simple Dialog'
    },
    content: {
      type: String,
      required: false,
      default: ''
    },
    dialogWidth: {
      type: Number,
      required: false,
      default: 600
    }
  },
  mounted () {
  }
}
</script>
