<template>
  <SimpleModalDlg
    :modalId="modalId"
    :title="title"
    content=""
    defaultButtonText=""
    :button2Text="''"
    :button2Action="button2Action"
    :dialogWidth="400"
  >
    <template v-slot:bodyPost>
      <div class="container-fluid">
        <div class="row mb-1">
          <div class="col-4">
            <span class="field-label"></span>
          </div>
          <div class="col">
            <input class="mr-1" style="width:10em;" v-model="username"/>
          </div>
        </div>
        <div class="row">
          <div class="col-4">
            <span class="field-label"></span>
          </div>
          <div class="col">
            <input class="mr-1" style="width:10em;" type="password" v-model="password"/>
          </div>
        </div>
      </div>
    </template>
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'
import {setToken} from '@/utils/auth'

export default {
  name: 'LoginDlg',
  components: {
    SimpleModalDlg,
  },
  data () {
    return {
      username: '',
      password: ''
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: false,
      default: ''
    }
  },
  computed: {
  },
  methods: {
    button2Action () {
      let endpoint = 'api-token-auth/'
      apis.apiAjaxPost(endpoint,
        { username: this.username, password:this.password },
        (res) => {
          // console.log('Login success', res)
          setToken(res['token'])
          this.$emit('login-success')
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
  },
  watch: {
  },
  mounted () {
  }
}
</script>
