<template>
  <SimpleModalDlg
    :modalId="modalId"
    title=""
    :content="`${cno}`"
    defaultButtonText=""
    button2Text=""
    :button2Action="button2Action" :dialogWidth="300">
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'

export default {
  name: 'ChannelDeleteDlg',
  components: {
    SimpleModalDlg,
  },
  data () {
    return {
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    channelId: {
      type: Number,
      required: true
    },
    cno: {
      type: Number,
      required: true
    }
  },
  computed: {
  },
  methods: {
    button2Action () {
      let endpoint = `channel/channels/${this.channelId}/`

      let data = {}

      apis.apiAjaxDelete(endpoint, data,
        () => {
          // if (res.code === 0) {
          alert('')
          this.$emit('channel-deleted')
          // } else {
          //   alert(`(${res.code}): ${res.message}`)
          // }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
  },
  watch: {
  },
  mounted () {
  }
}
</script>
