<template>
  <div class="container-fluid" style="height: 100vh">
    <div class="row " style="height: 100vh">
      <div class="col-xl-2 col-lg-2 col-md-3 col-sm-3 bg-dark">
        <TopNavBar></TopNavBar>
      </div>
      <div class="col">
        <div class="row mt-2 mb-2 mr-2 border-bottom" style="height: 32px;justify-content: right;">
          {{dateString}}
        </div>
        <div class="row">
          <router-view></router-view>
        </div>
      </div>
    </div>
    <ActivateDlg
      :modalId="authModalId"
      @auth-success="authSuccess"
    ></ActivateDlg>
    <LoginDlg
      :modalId="loginModalId"
      :title="projectName || ''"
      @login-success="loginSuccess"
    ></LoginDlg>
  </div>
</template>

<script>
import ModuleSysConfig from '@/components/modules/ModuleSysConfig'
import ModuleChConfig from '@/components/modules/ModuleChConfig'
import ModuleAlertConfig from '@/components/modules/ModuleAlertConfig'
import ModuleAlerts from '@/components/modules/ModuleAlerts'
import ModuleAlgorithm from './modules/ModuleAlgorithm'
import TopNavBar from './TopNavBar.vue'
import ActivateDlg from './modules/ActivateDlg'
import LoginDlg from './modules/LoginDlg'
import { getToken, removeToken, setProjectInfo, getProjectInfo } from '@/utils/auth'

import dayjs from 'dayjs'
import utils from '@/utils/utils'
import $ from 'jquery'
import apis from '@/apis/apis'

export default {
  name: 'MainPanel',
  children: [
    {path: 'ModuleSysConfig', name: 'ModuleSysConfig', component: ModuleSysConfig},
    {path: 'ModuleChConfig', name: 'ModuleChConfig', component: ModuleChConfig},
    {path: 'ModuleAlertConfig', name: 'ModuleAlertConfig', component: ModuleAlertConfig},
    {path: 'ModuleAlerts', name: 'ModuleAlerts', component: ModuleAlerts},
    {path: 'ModuleAlgorithm', name: 'ModuleAlgorithm', component: ModuleAlgorithm}
  ],
  components: {
    TopNavBar,
    ActivateDlg,
    LoginDlg
},
  data () {
    return {
      authModalId: utils.randomId('modalId'),
      loginModalId: utils.randomId('loginModalId'),
      dateString: '',
      loggedIn: getToken(),
    }
  },
  computed: {
    projectName () {
      let pi = getProjectInfo()
      if (pi) {
        return pi.name
      }
      return null
    }
  },
  methods: {
    authSuccess () {
      let endpoint = 'system/projectinfos/'
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          // console.log(endpoint, res)
          if (res.code === 0 && res.data.length) {
            setProjectInfo(res.data[0])
            $(`#${this.loginModalId}`).modal('show')
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    loginSuccess () {
      this.loggedIn = !!getToken()
    }
  },
  mounted() {
    setInterval(() => {
      this.dateString = dayjs().format('YYYYMMDD HH:mm:ss')
    }, 1000)
    setTimeout(() => {
      let endpoint = 'system/projectinfos/'
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          // console.log(endpoint, res)
          if (res.code === 0) {
            if (!res.data.length) {
              removeToken()
              $(`#${this.authModalId}`).modal('show')
            } else {
              setProjectInfo(res.data[0])
              if (!this.loggedIn) {
                $(`#${this.loginModalId}`).modal('show')
              }
            }
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }, 200)
  }
}

</script>