<template>
  <SimpleModalDlg :modalId="modalId" title="" content="" defaultButtonText="" button2Text=""
    :button2Action="button2Action" :dialogWidth="600">
    <template v-slot:bodyPost>
      <div class="container-fluid">
        <div class="row">
          <div class="col-4">
            <span class="field-label"></span>
          </div>
          <div class="col mb-1">
            <input class="mr-1" style="width:20em;" v-model="projectName" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="projectNameValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
          <div class="col-4">
            <span class="field-label"></span>
          </div>
          <div class="col mb-1">
            <input class="mr-1" style="width:20em;" v-model="authCode" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="authCodeValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
          <div class="col-4">
            <span class="field-label"></span>
          </div>
          <div class="col mb-1">
            <input class="mr-1" style="width:20em;" v-model="username" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="usernameValid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
        <div class="row mb-1">
          <div class="col-4">
            <span class="field-label"></span>
          </div>
          <div class="col">
            <input class="mr-1" style="width:20em;" type="password" v-model="password1" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="password1Valid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
        <div class="row mb-1">
          <div class="col-4">
            <span class="field-label">()</span>
          </div>
          <div class="col">
            <input class="mr-1" style="width:20em;" type="password" v-model="password2" />
            <svg class="icon svg-icon" aria-hidden="true">
              <use v-if="password2Valid" xlink:href="#icon-dagou"></use>
              <use v-else xlink:href="#icon-closel"></use>
            </svg>
          </div>
        </div>
      </div>
    </template>
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'

export default {
  name: 'ActivateDlg',
  components: {
    SimpleModalDlg,
  },
  data () {
    return {
      username: '',
      password1: '',
      password2: '',
      projectName: '',
      authCode: '',
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    }
  },
  computed: {
    projectNameValid () {
      return this.projectName.trim().length > 0
    },
    authCodeValid () {
      return this.authCode.trim().length == 32
    },
    usernameValid () {
      return this.username.trim().length > 0
    },
    password1Valid () {
      return this.password1.length >= 6
    },
    password2Valid () {
      return this.password2.length >= 6 && this.password1 === this.password2
    }
  },
  methods: {
    button2Action () {
      let endpoint = 'system/projectinfos/activate/'

      let data = {}

      // 
      if (!this.projectNameValid) {
        alert('')
        return
      }
      data['project_name'] = this.projectName.trim()

      if (!this.authCodeValid) {
        alert('')
        return
      }
      data['auth_code'] = this.authCode.trim()
    
      if (!this.usernameValid) {
        alert('')
        return
      }
      data['username'] = this.username.trim()

      if (this.password1 !== this.password2) {
        alert('')
        return
      }
      if (!this.password1Valid) {
        alert('')
        return
      }
      data['password'] = this.password1

      apis.apiAjaxPost(endpoint, data,
        (res) => {
          console.log('Activate return', res)
          if (res.code === 0) {
            alert('')
            this.$emit('auth-success')
          } else {
            alert(`(${res.code}): ${res.message}`)
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
  },
  watch: {
  },
  mounted () {
  }
}
</script>
