<template>
  <div class="container-fluid">
    <div class="row w-100 ml-1 my-2">
      <span style="display: flex; align-items: center;"><b></b></span>
      <button class="btn btn-light ml-4" :disabled="true"> {{usedNos.length}}/{{availNos.length}}</button>
    </div>
    <div class="row w-100 row-cols-xl-6 row-cols-md-3 row-cols-sm-3 text-center" style="width: 18rem;">
      <div class="col mb-4" v-for="item, i in Object.keys(algNameIcons)" :key="'alg' + uniqueId + i" style="padding-bottom: 0%;">
        <div class="card border-0 h-100">
          <img src="../../assets/algo-bg.png" class="card-img"/>
          <div class="card-img-overlay">
            <div class="iconbox">
              <img :src="algNameIcons[item].icon" class="card-img-top" :alt="algNameIcons[item].name" style="width: 50px;"/>
            </div>
            <div class="card-body">
              <p class="card-text"><b>{{algNameIcons[item].name}}</b></p>
              <p><img :src="cameraImg"/><b>0</b></p>
              <a href="#" class="btn btn-primary"></a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import utils from '@/utils/utils'
import apis from '@/apis/apis'

export default {
  name: 'ModuleAlgorithm',
  components: {
  },
  data () {
    return {
      uniqueId: utils.randomId('unique'),
      algNameIcons: {
        safehelmet: {name: '', icon: require('../../assets/icon/algorithm_icon/1.png')},
        vest: {name: '', icon: require('../../assets/icon/algorithm_icon/2.png')},
        fire: {name: '', icon: require('../../assets/icon/algorithm_icon/3.png')},
        motor: {name: '', icon: require('../../assets/icon/algorithm_icon/4.png')},
        electomotor: {name: '', icon: require('../../assets/icon/algorithm_icon/5.png')},
        cigarette: {name: '', icon: require('../../assets/icon/algorithm_icon/6.png')},
        human: {name: '', icon: require('../../assets/icon/algorithm_icon/7.png')},
        plate: {name: '', icon: require('../../assets/icon/algorithm_icon/8.png')},
        mobile: {name: '', icon: require('../../assets/icon/algorithm_icon/9.png')},
        vehicle: {name: '', icon: require('../../assets/icon/algorithm_icon/10.png')},
        animal: {name: '', icon: require('../../assets/icon/algorithm_icon/11.png')},
        facemask: {name: '', icon: require('../../assets/icon/algorithm_icon/12.png')},
      },
      cameraImg: require('../../assets/icon/camera.png'),
      freeNos: [1, 2, 3, 4, 5, 6, 7, 8],
      usedNos: [],
    }
  },
  computed: {
    availNos () {
      let nos = [...this.freeNos, ...this.usedNos]
      return nos.sort()
    }

  },
  methods: {
    getUsedFreeChannels () {
      let endpoint = 'channel/channels/get_used_free_channel_nos/'

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            this.freeNos = res.data.free_nos
            this.usedNos = res.data.used_nos
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  mounted () {
    this.getUsedFreeChannels()
  }
}

</script>

<style>
.iconbox{
  display: flex;
  height: 100px;
  align-items: center;
  justify-content: center;
}
</style>
